<template>
  <div id="articleList">
    <articles v-for='article in articleList' :key='article.pk' :info='article.fields' :pk='article.pk'></articles>
  </div>
</template>
<script>
import articles from '../components/articles.vue'
export default {
  name: 'articleList',
  components: {
    articles
  },
  data () {
    return {
      articleList: null
    }
  },
  created () {
    this.getArticles('1')
  },
  methods: {
    getArticles (n) {
      this.axios({
        url: 'https://api.hzxnoheya.top/api/articleList',
        method: 'get',
        params: { page: n }
      })
        .then(res => {
          this.articleList = res.data
        })
        .catch(error => { console.log(error) })
    }
  }

}

</script>
<style scoped>
#articleList{
    min-height: 700px;
    width: 100%;
    border-top: 1px solid white;
    border-radius: 0 0 8px 8px;
    background: #dedede;
    padding: 2em 0;
}
</style>
