<template>
    <div id="main">
    <article>
        <header><h3>{{title}}</h3></header>
        <div id="pictureWrap">
          <div id="scaleImgWrap">
            <img id="articlePictures" :src='imageUrl' alt="articlePicture">
          </div>
        </div>
        <section v-html="preview"></section>
        <footer>
            <p>{{time[0]}}</p>
            <p>{{time[1]}}</p>
            <div id="detailButton"><router-link id="detailUrl" :to="routeUrl+id">Read More →</router-link></div>
        </footer>
    </article>
    </div>
</template>
<script>
export default {
  name: 'articles',
  props: ['info', 'pk'],
  data () {
    return {
      routeUrl: '/article/detail/',
      imageUrl: this.info.picture,
      title: this.info.title,
      preview: this.info.articlePreface,
      time: this.info.createdTime.split('T'),
      id: this.pk
    }
  }
}
</script>
<style scoped>
#main{
    width: 90%;
    height:fit-content ;
    margin: 2em auto;
    background: #d7c4bb;
    padding: 1em;
    position: relative;
    border-radius:1em 2em 1em;
}
#pictureWrap{
  width: 100%;
  margin: 1em 0;
}
#scaleImgWrap{
  overflow: hidden;
  margin:0 auto;
  width: 80%;
  border-radius: 1em;
}
#articlePictures{
  max-width: 100%;
  transition: 0.6s;
}
#articlePictures:hover{
  transform: scale(1.2);
}
footer{
    position: relative;
}
#detailButton{
    position:absolute;
    right: 1em;
    bottom: 0.5em;
    width: 8em;
    text-align:center;
    background: white;
    border-radius: 2px;
    transition: 0.5s;
}
#detailButton:hover{
  background: #7db9de;
}
#detailUrl{
  color: #7db9de;
}
#detailUrl:hover{
  color: white;
  transition: 0.5s;
}

</style>
